<template>
  <header id="header-pc">
    <div class="pc-logo">
      <img class="logo" src="../../assets/images/logo.png" alt="">
    </div>
    <div class="pc-right-nav">
      <div class="nav">
        <div @click="jumpNav(item.active)" class="nav-link" :class="{ active: item.active === activeNav}" v-for="item in navs" :key="item.active">
          {{ item.name }}
        </div>
      </div>
    </div>
  </header>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      navs: [
        { name: '官网首页', active: 'index' },
        { name: '关于我们', active: 'about' },
        { name: '应急装备', active: '2' },
        { name: '解决方案', active: 'solve' },
        { name: '新闻资讯', active: '4' },
        { name: '招贤纳士', active: '5' },
        { name: '联系我们', active: '6' },
      ],
      activeNav: '0'
    }
  },

  created() {
    this.activeNav = this.$route.meta.active;
  },

  methods: {
    jumpNav(v) {
      console.log(v, '=-=');
      if (v === 'index') {
        this.$router.push(`/`)
      } else {
        this.$router.push(`/${v}`)
      }
      this.activeNav = v
    }
  }
};
</script>

<style lang="less">
#header-pc {
  width: 100%;
  height: 90px;
  display: flex;
  .pc-logo {
    width: 30%;
    height: 100%;
    .logo {
      margin-left: 120px;
      margin-top: 16px;
    } 
  }
  .pc-right-nav {
    flex: 1;

    .nav {
      display: flex;
      justify-content: space-around;
      height: 100%;
      align-items: end;
      .nav-link {
        cursor: pointer;
        position: relative;
        font-size: 16px;
        transition: color 0.1s, border-bottom 0.1s; /* 平滑过渡效果 */
        &:hover {
          font-size: 20px;
          color: red;
        }
        padding-bottom: 5px;
      }
      .nav-link.active {
        font-size: 20px;
        color: red;
      }
      .nav-link:hover:after,
      .nav-link.active:after {
        content: ''; /* 必须有内容，即使是空的 */
        position: absolute; /* 定位伪元素 */
        left: 50%; /* 水平居中 */
        bottom: 0; /* 底部对齐 */
        width: 50px; /* 边框的长度 */
        height: 2px; /* 边框的高度 */
        background-color: red; /* 边框的颜色 */
        transform: translateX(-50%); /* 将伪元素向左移动自身宽度的50%，实现居中对齐 */
      }
    }
  }
}
</style>
